import React from "react";
import Typist from "react-typist";
import { VelocityTransitionGroup } from "velocity-animate";

const TeleHero = props => (
  <div>
    <VelocityTransitionGroup enter={{animation: "slideDown"}} leave={{animation: "slideUp"}}>
    </VelocityTransitionGroup>
      <a
        class="link dim black b f2 f-headline-ns tc db mb3 mb4-ns monoton"
        href="#"
        title="Home"
      >
        TELESOUND
      </a>
    <Typist>
      <header class="tc ph4">
        <div class="f4 f4-m f3-l fw2 black-80 mt0 lh-copy marker">
          TV, Home Theater, CCTV, Audio
        </div>
      </header>
    </Typist>
  </div>
);

export default TeleHero;



